<template>
  <div id="login">
    <div class="login-box">
      <h1> Login </h1>
      <div class="text-box">
        <i class="fas fa-user-alt"></i> 
        <input v-model="username" name="username" placeholder="E-mail"> 
      </div>
      
      <div class="text-box">
        <i class="fas fa-lock"></i>
        <input v-model="password" type="password" name="password" placeholder="Password">
      </div>      
      <a :href="url.signUpUrl" ></a> 
      <a style="float:right;" :href="url.forgotUrl" >   ? </a>
      <div class="submit">
              <button class="btn" v-on:click="login">login</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
        username: '',
        password: '',
        url: {
           signUpUrl: '/signUp',
           forgotUrl: '/forgot'
        }
    }
  },
   methods: {
    login: function (event) {
      this.$http.post('/api/login', {
        username: this.username,
        password: this.password
      })
      .then((response) => {  
            alert('success login' + response.data.username)
            console.log(response.data.session);
            let token = response.data.session;
            let username = response.data.username;
            let nickname = response.data.nickname;
            let isAdmin = response.data.isAdmin
            // node     access_token  token .
            localStorage.setItem("access_token", token);
            localStorage.setItem("username", username);
            localStorage.setItem("nickname", nickname);
            localStorage.setItem("isAdmin", isAdmin);
            this.$store.state.username = username;
            this.$store.state.nickname = nickname;
            this.$store.state.token = token;
            this.$store.state.isAdmin = isAdmin;
            // 
            this.$router.push('/');
       
        },
        (error) => { 
          alert('undifind error: ' +error.response.data.error)
        }
      )
      .catch(error => {
        alert('catch error: ' + error)
      })
    }
  }
}
</script>

<style src="./login.css">
</style>